import React from 'react';
import { Button } from './Button';

export class Counter extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      counter: 0
    };

    this.counterUp = this.counterUp.bind(this);
    this.counterDown = this.counterDown.bind(this);
    this.counterReset = this.counterReset.bind(this);
  }

  counterUp() {
    this.setState((prevState) => {
      return {counter: prevState.counter + 1};
    });
  }

  counterDown() {
    this.setState((prevState) => {
      return {counter: prevState.counter - 1};
    });
  }

  counterReset() {
    this.setState({counter: 0});
  }

  render() {
    return(
      <div>
        <h1>Counter: {this.state.counter}</h1>
        <Button onClick={this.counterUp} text='+1' className='btn' />
        <Button onClick={this.counterDown} text='-1' className='btn' />
        <Button onClick={this.counterReset} text='reset' className='btn' />
      </div>
    );
  }
}



